.rain {
  width: 4px;
  height: 4px;
  background: linear-gradient(#000, #0f0, #000);
  border-radius: 50%;
  animation: animatrain 3s linear infinite;
  position: absolute;
  top: 50%;
  left: 50%;
}

.rain:before {
  content: "";
  width: 300px;
  height: 1px;
  background: linear-gradient(90deg, #000, #0f0, #000);
  animation: animateeee 20s linear infinite;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}

@keyframes animatrain {
  0% {
    opacity: 1;
    transform: rotate(315deg)translateX(0);
  }

  70% {
    opacity: 1;
  }

  100% {
    opacity: 0;
    transform: rotate(315deg)translateX(-800px);
  }
}

.rain:nth-child(1) {
  top: 0;
  right: 0;
  left: initial;
  animation-delay: 0;
  animation-duration: 1s;
}

.rain:nth-child(2) {
  top: 0;
  right: 80px;
  left: initial;
  animation-duration: 3s;
  animation-delay: -.2s;
}

.rain:nth-child(3) {
  top: 80px;
  right: 0;
  left: initial;
  animation-duration: 2s;
  animation-delay: .4s;
}

.rain:nth-child(4) {
  top: 0;
  right: 180px;
  left: initial;
  animation-duration: 1.5s;
  animation-delay: .6s;
}

.rain:nth-child(5) {
  top: 0;
  right: 400px;
  left: initial;
  animation-duration: 2.5s;
  animation-delay: .8s;
}

.rain:nth-child(6) {
  top: 0;
  right: 600px;
  left: initial;
  animation-duration: 3s;
  animation-delay: 1s;
}

.rain:nth-child(7) {
  top: 300px;
  right: 0;
  left: initial;
  animation-duration: 1.75s;
  animation-delay: 1.2s;
}

.rain:nth-child(8) {
  top: 0;
  right: 700px;
  left: initial;
  animation-duration: 1.25s;
  animation-delay: 1.4s;
}

.rain:nth-child(9) {
  top: 0;
  right: 1000px;
  left: initial;
  animation-duration: 1.75s;
  animation-delay: .75s;
}

.rain:nth-child(10) {
  top: 0;
  right: 450px;
  left: initial;
  animation-duration: 2.25s;
  animation-delay: 2.75s;
}

