.signin {
  position: absolute;
  top: 120px;
  left: 380px;
  pointer-events: all;
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  background: #222;
  z-index: 1000;
  width: 400px;
  padding: 40px;
  border-radius: 4px;
  box-shadow: 0 15px 35px rgba(0, 0, 0, 0.5);
}
.signin .content {
  position: relative;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  gap: 40px;
}
.signin .content h1 {
  font-size: 2em;
  color: #0f0;
  animation: animateeee 20s linear infinite;
  text-transform: uppercase;
}
.signin .content .form {
  width: 100%;
  display: flex;
  flex-direction: column;
  gap: 25px;
}
.signin .content .form .inputBx {
  position: relative;
  width: 100%;
}
.signin .content .form .inputBx input {
  position: relative;
  width: 100%;
  background: #333;
  border: none;
  outline: none;
  padding: 25px 10px 7.5px;
  border: 4px;
  color: #fff;
  font-weight: 500;
  font-size: 1em;
}
.signin .content .form .inputBx i {
  position: absolute;
  left: 0;
  padding: 15px 10px;
  font-style: normal;
  color: #aaa;
  transition: 0.5s;
  pointer-events: none;
}
.signin .content .form .inputBx input:focus ~ i,
.signin .content .form .inputBx input:valid ~ i {
  transform: translateY(-7.5px);
  font-size: 0.8em;
  color: #fff;
}
.signin .content .form .links {
  position: relative;
  width: 100%;
  display: flex;
  justify-content: space-between;
}
.signin .content .form .links a {
  color: #fff;
  text-decoration: none;
}
.signin .content .form .links a:nth-child(2) {
  color: #0f0;
  animation: animateeee 20s linear infinite;
  font-weight: 600;
}
.signin .content .form .inputBx input[type='submit'] {
  padding: 10px;
  background: #0f0;
  animation: animateeee 20s linear infinite;
  color: #111;
  font-weight: 600;
  font-size: 1.25em;
  letter-spacing: 0.05em;
  cursor: pointer;
}
